<template>
  <div>
    <!--  -->
    <el-breadcrumb separator-class="el-icon-arrow-right">
      <el-breadcrumb-item :to="{ path: '/welcome' }"></el-breadcrumb-item>
      <el-breadcrumb-item></el-breadcrumb-item>
      <el-breadcrumb-item></el-breadcrumb-item>
    </el-breadcrumb>

    <!--  -->
    <el-card>
      <!--  -->
      <el-alert
        title=""
        type="warning"
        show-icon
        :closable="false"
      >
      </el-alert>

      <!--  -->
      <el-row>
        <el-col>
          <span></span>
          <!--  -->
          <el-cascader
            v-model="selectedKeys"
            :options="cateList"
            :props="{
              expandTrigger: 'hover',
              label: 'cat_name',
              value: 'cat_id',
              children: 'children',
            }"
            @change="handleChange"
          ></el-cascader>
        </el-col>
      </el-row>

      <!-- tabs -->
      <el-tabs v-model="activeName" @tab-click="handleTabClick">
        <!--  -->
        <el-tab-pane label="" name="many">
          <el-button
            type="primary"
            size="mini"
            :disabled="isBtnDisable"
            @click="showAddDialog"
            ></el-button
          >

          <el-table :data="manyTableData" style="width: 100%">
            <!--  -->
            <el-table-column type="expand">
              <template slot-scope="scope">
                <el-tag
                  v-for="(item , i) in scope.row.attr_vals"
                  :key="item.attr_id"
                  closable
                  @close='handleClosed(i, scope.row)'
                  >{{ item }}</el-tag
                >
                <el-input
                  class="input-new-tag"
                  v-if="scope.row.ManyInputVisible"
                  v-model="scope.row.ManyInputValue"
                  ref="ManyInputRef"
                  size="small"
                  @keyup.enter.native="handleInputConfirm(scope.row)"
                  @blur="handleInputConfirm(scope.row)"
                ></el-input>
                <el-button
                  v-else
                  class="button-new-tag"
                  size="small"
                  @click="showManyInput(scope.row)"
                  >+ New Tag</el-button
                >
              </template>
            </el-table-column>
            <!--  -->
            <el-table-column type="index" label="#"> </el-table-column>
            <el-table-column prop="attr_name" label="">
            </el-table-column>
            <el-table-column label="">
              <template slot-scope="scope">
                <el-button
                  size="mini"
                  type="primary"
                  icon="el-icon-edit"
                  @click="showEditDialog(scope.row)"
                  ></el-button
                >
                <el-button
                  size="mini"
                  type="danger"
                  icon="el-icon-delete"
                  @click="showRemoveDialog(scope.row)"
                  ></el-button
                >
              </template>
            </el-table-column>
          </el-table>
        </el-tab-pane>
        <!--  -->
        <el-tab-pane label="" name="only">
          <el-button
            type="primary"
            size="mini"
            :disabled="isBtnDisable"
            @click="showAddDialog"
            ></el-button
          >

          <el-table :data="onlyTableData" style="width: 100%" border stripe>
            <!--  -->
            <el-table-column type="expand">
              <template slot-scope="scope">
                <el-tag
                  v-for="item in scope.row.attr_vals"
                  :key="item.attr_id"
                  closable
                  >{{ item }}</el-tag
                >
                <el-input
                  class="input-new-tag"
                  v-if="scope.row.onlyInputVisible"
                  v-model="scope.row.onlyInputValue"
                  ref="onlyInputRef"
                  size="small"
                  @keyup.enter.native="handleOnlyInputConfirm(scope.row)"
                  @blur="handleOnlyInputConfirm(scope.row)"
                ></el-input>
                <el-button
                  v-else
                  class="button-new-tag"
                  size="small"
                  @click="showonlyInput(scope.row)"
                  >+ New Tag</el-button
                >
              </template>
            </el-table-column>
            <!--  -->
            <el-table-column type="index" label="#"> </el-table-column>
            <el-table-column prop="attr_name" label="">
            </el-table-column>
            <el-table-column label="">
              <template slot-scope="scope">
                <el-button
                  size="mini"
                  type="primary"
                  icon="el-icon-edit"
                  @click="showEditDialog(scope.row)"
                  ></el-button
                >
                <el-button
                  size="mini"
                  type="danger"
                  icon="el-icon-delete"
                  @click="showRemoveDialog(scope.row)"
                  ></el-button
                >
              </template>
            </el-table-column>
          </el-table>
        </el-tab-pane>
      </el-tabs>
    </el-card>

    <!--  -->
    <el-dialog
      :title="'' + titleText"
      :visible.sync="addDialogVisible"
      width="50%"
      @close="addDialogClosed"
    >
      <!--  -->
      <el-form
        :model="AddForm"
        :rules="AddFormrules"
        ref="AddFormRef"
        label-width="100px"
      >
        <el-form-item :label="titleText" prop="attr_name">
          <el-input v-model="AddForm.attr_name"></el-input>
        </el-form-item>
      </el-form>
      <span slot="footer" class="dialog-footer">
        <el-button @click="addDialogVisible = false"> </el-button>
        <el-button type="primary" @click="addParams"> </el-button>
      </span>
    </el-dialog>

    <!--  -->
    <el-dialog
      :title="'' + titleText"
      :visible.sync="editDialogVisible"
      width="50%"
    >
      <el-form
        :model="editForm"
        :rules="AddFormrules"
        ref="editFormRef"
        label-width="100px"
      >
        <el-form-item :label="titleText" prop="attr_name">
          <el-input v-model="editForm.attr_name"></el-input>
        </el-form-item>
      </el-form>

      <span slot="footer" class="dialog-footer">
        <el-button @click="editDialogVisible = false"> </el-button>
        <el-button type="primary" @click="editParams"> </el-button>
      </span>
    </el-dialog>
  </div>
</template>

<script>
export default {
  data() {
    return {
      // 
      cateList: [],
      //   
      selectedKeys: [],
      activeName: 'many',
      //   
      manyTableData: [],
      //   
      onlyTableData: [],
      addDialogVisible: false,
      AddForm: {},
      AddFormrules: {
        attr_name: [
          {
            required: true,
            message: '',
            trigger: 'blur',
          },
        ],
      },
      editDialogVisible: false,
      editForm: {
        attr_name: '',
      },
      edit_Attr_Id: '',
      // ManyInputVisible: false,
      // ManyInputValue: '',
      // onlyInputVisible:false,
      // onlyInputValue: '',
    }
  },
  created() {
    this.getCateList()
  },
  methods: {
    async getCateList() {
      const { data: res } = await this.$http.get(`categories`, {
        params: { type: 3 },
      })
      if (res.meta.status !== 200) {
        return this.$message.error('')
      }
      this.cateList = res.data
      //   console.log(this.cateList)
    },
    handleChange() {
      this.getParamsData()
    },
    handleTabClick() {
      this.getParamsData()
    },
    async getParamsData() {
      if (this.selectedKeys.length !== 3) {
        this.selectedKeys = []
        this.manyTableData =[]
        this.onlyTableData = []
        return
      }
      const { data: res } = await this.$http.get(
        `categories/${this.cateId}/attributes`,
        {
          params: { sel: this.activeName },
        }
      )
      if (res.meta.status !== 200) {
        return this.$message.error(``)
      }

      if (this.activeName === 'many') {
        res.data.forEach((item) => {
          item.attr_vals = item.attr_vals ? item.attr_vals.split(',') : []
          item.ManyInputVisible = false
          item.ManyInputValue = ''
        })
        this.manyTableData = res.data
        console.log(this.manyTableData)
      } else {
        res.data.forEach((item) => {
          item.attr_vals = item.attr_vals ? item.attr_vals.split(',') : []
          item.onlyInputVisible = false
          item.onlyInputValue = ''
        })
        this.onlyTableData = res.data
        console.log(this.onlyTableData)
      }
    },
    showAddDialog() {
      this.addDialogVisible = true
    },
    addDialogClosed() {
      this.$refs.AddFormRef.resetFields()
    },
    // /
    addParams() {
      this.$refs.AddFormRef.validate(async (valid) => {
        if (!valid) return
        const { data: res } = await this.$http.post(
          `categories/${this.cateId}/attributes`,
          { attr_name: this.AddForm.attr_name, attr_sel: this.activeName }
        )
        if (res.meta.status !== 201) {
          return this.$message.error('')
        }
        this.$message.success('')
        this.getParamsData()
        this.addDialogVisible = false
      })
    },
    // 
    async showEditDialog(row) {
      const { data: res } = await this.$http.get(
        `categories/${this.cateId}/attributes/${row.attr_id}`,
        { params: { attr_sel: this.activeName } }
      )
      if (res.meta.status !== 200) {
        return this.$message.error('')
      }
      this.editForm.attr_name = res.data.attr_name
      this.edit_Attr_Id = res.data.attr_id
      console.log(res.data)
      this.editDialogVisible = true
    },
    // 
    editParams() {
      this.$refs.editFormRef.validate(async (valid) => {
        if (!valid) return
        const { data: res } = await this.$http.put(
          `categories/${this.cateId}/attributes/${this.edit_Attr_Id}`,
          { attr_name: this.editForm.attr_name, attr_sel: this.activeName }
        )
        if (res.meta.status !== 200) {
          return this.$message.error('')
        }
        this.$message.success('')
        this.getParamsData()
        this.editDialogVisible = false
      })
    },
    // 
    async showRemoveDialog(row) {
      const confirmResult = await this.$confirm(
        ', ?',
        '',
        {
          confirmButtonText: '',
          cancelButtonText: '',
          type: 'warning',
        }
      ).catch((err) => err)
      if (confirmResult !== 'confirm') {
        return this.$message.info('')
      }
      const { data: res } = await this.$http.delete(
        `categories/${this.cateId}/attributes/${row.attr_id}`
      )
      if (res.meta.status !== 200) {
        this.$message.error('')
      }
      this.$message.success('')
      this.getParamsData()
    },
    //
    showManyInput(row) {
      row.ManyInputVisible = true
      // $nextTick
      this.$nextTick((_) => {
        this.$refs.ManyInputRef.$refs.input.focus()
      })
    },
    async handleInputConfirm(row) {
      if (row.ManyInputValue.trim().length === 0) {
        row.ManyInputValue = ''
        row.ManyInputVisible = false
        return
      }
      row.attr_vals.push(row.ManyInputValue.trim())
      row.ManyInputValue = ''
      this.saveAttrVals(row)
    },
    showonlyInput(row){
      row.onlyInputVisible = true
      this.$nextTick((_) => {
        this.$refs.onlyInputRef.$refs.input.focus()
      })
    },
    async handleOnlyInputConfirm(row) {
      if (row.onlyInputValue.trim().length === 0) {
        row.onlyInputValue = ''
        row.onlyInputVisible = false
        return
      }
      row.attr_vals.push(row.onlyInputValue.trim())
      row.onlyInputValue = ''
      this.saveAttrVals(row)
    },
    async saveAttrVals(row){
      const { data: res } = await this.$http.put(
        `categories/${this.cateId}/attributes/${row.attr_id}`,
        {
          attr_name: row.attr_name,
          attr_sel: row.attr_sel,
          attr_vals: row.attr_vals.join(','),
        }
      )
      if (res.meta.status !== 200) {
        return this.$message.error('')
      }
      this.$message.success('')
      row.onlyInputVisible = false
    },
    handleClosed(i, row){
      row.attr_vals.splice(i,1)
      this.saveAttrVals(row)
    }
  },
  computed: {
    //   true,false
    isBtnDisable() {
      if (this.selectedKeys.length !== 3) {
        return true
      }
      return false
    },
    // id
    cateId() {
      if (this.selectedKeys.length === 3) {
        return this.selectedKeys[2]
      }
      return null
    },
    titleText() {
      if (this.activeName === 'many') {
        return ''
      } else {
        return ''
      }
    },
  },
}
</script>

<style lang="less" scoped>
.el-tag {
  margin: 10px 15px;
}
.input-new-tag {
  width: 150px;
}
</style>