<template>
  <div>
    <!--  -->
    <el-breadcrumb separator-class="el-icon-arrow-right">
      <el-breadcrumb-item :to="{ path: '/welcome' }"></el-breadcrumb-item>
      <el-breadcrumb-item></el-breadcrumb-item>
      <el-breadcrumb-item></el-breadcrumb-item>
    </el-breadcrumb>

    <!--  -->
    <el-card>
      <!--  -->
      <el-row :gutter="20">
        <!--  -->
        <el-col :span="8">
          <el-input
            placeholder=""
            v-model="queryInfo.query"
            clearable
          >
            <el-button
              slot="append"
              icon="el-icon-search"
              @click="getGoodsList"
            ></el-button>
          </el-input>
        </el-col>
        <!--  -->
        <el-col :span="4">
          <el-button type="primary" @click="addPage"
            ></el-button
          >
        </el-col>
      </el-row>

      <!--  -->
      <el-table :data="goodsList" style="width: 100%" border stripe>
        <el-table-column type="index" label="#"> </el-table-column>
        <el-table-column prop="goods_name" label=""> </el-table-column>
        <el-table-column prop="goods_price" label="" width="95px">
        </el-table-column>
        <el-table-column prop="goods_weight" label="" width="70px">
        </el-table-column>
        <el-table-column prop="add_time" label="" width="170px">
          <template slot-scope="scope">
            {{ scope.row.add_time | dateFormat }}
          </template>
        </el-table-column>
        <el-table-column label="" width="130px">
          <template slot-scope="scope">
            <el-button
              type="primary"
              icon="el-icon-edit"
              size="mini"
              @click="showEditGoodsDialog(scope.row)"
            ></el-button>
            <el-button
              type="danger"
              icon="el-icon-delete"
              size="mini"
              @click="removeGoods(scope.row)"
            ></el-button>
          </template>
        </el-table-column>
      </el-table>

      <!--  -->
      <el-pagination
        @size-change="handleSizeChange"
        @current-change="handleCurrentChange"
        :current-page="queryInfo.pagenum"
        :page-sizes="[10, 15, 20, 30]"
        :page-size="queryInfo.pagesize"
        layout="total, sizes, prev, pager, next, jumper"
        :total="total"
        background
      >
      </el-pagination>
    </el-card>

    <!--  -->
    <!-- <el-dialog title="" :visible.sync="addGoodsDialogVisible" width="50%">
      <el-form
        :model="addGoodsForm"
        :rules="goodsFormRules"
        ref="addGoodsFormRef"
        label-width="100px"
      >
        <el-form-item label="" prop="goods_name">
          <el-input v-model="addGoodsForm.goods_name"></el-input>
        </el-form-item>
        <el-form-item label="" prop="goods_price">
          <el-input v-model="addGoodsForm.goods_price" type="number"></el-input>
        </el-form-item>
        <el-form-item label="" prop="goods_weight">
          <el-input
            v-model="addGoodsForm.goods_weight"
            type="number"
          ></el-input>
        </el-form-item>
        <el-form-item label="" prop="goods_number">
          <el-input
            v-model="addGoodsForm.goods_number"
            type="number"
          ></el-input>
        </el-form-item>
        <el-form-item label="" prop="goods_cat">
          <el-cascader
            v-model="selectedKeys"
            :options="cateList"
            :props="{
              expandTrigger: 'hover',
              label: 'cat_name',
              value: 'cat_id',
            }"
            @change="handleChange"
          ></el-cascader>
        </el-form-item>
      </el-form>
      <span slot="footer" class="dialog-footer">
        <el-button @click="addGoodsDialogVisible = false"> </el-button>
        <el-button type="primary" @click="addGoods"> </el-button>
      </span>
    </el-dialog> -->

    <!--  -->
    <el-dialog
      title=""
      :visible.sync="editGoodsDialogVisible"
      width="50%"
    >
      <!--  -->
      <el-form
        :model="editGoodsForm"
        :rules="goodsFormRules"
        ref="editGoodsFormRef"
        label-width="100px"
      >
        <el-form-item label="" prop="goods_name">
          <el-input v-model="editGoodsForm.goods_name"></el-input>
        </el-form-item>
        <el-form-item label="" prop="goods_price">
          <el-input
            v-model="editGoodsForm.goods_price"
            type="number"
          ></el-input>
        </el-form-item>
        <el-form-item label="" prop="goods_weight">
          <el-input
            v-model="editGoodsForm.goods_weight"
            type="number"
          ></el-input>
        </el-form-item>
        <el-form-item label="" prop="goods_number">
          <el-input
            v-model="editGoodsForm.goods_number"
            type="number"
          ></el-input>
        </el-form-item>
      </el-form>
      <span slot="footer" class="dialog-footer">
        <el-button @click="editGoodsDialogVisible = false"> </el-button>
        <el-button type="primary" @click="editGoods"
          > </el-button
        >
      </span>
    </el-dialog>
  </div>
</template>

<script>
export default {
  data() {
    return {
      queryInfo: {
        query: '',
        pagesize: 10,
        pagenum: 1,
      },
      goodsList: [],
      total: 0,
      addGoodsDialogVisible: false,
      addGoodsForm: {
        goods_name: '',
        goods_price: 0,
        goods_weight: 0,
        goods_number: 0,
      },
      goodsFormRules: {
        goods_name: [
          { required: true, message: '', trigger: 'blur' },
        ],
        goods_price: [
          { required: true, message: '', trigger: 'blur' },
        ],
        goods_weight: [
          { required: true, message: '', trigger: 'blur' },
        ],
        goods_number: [
          { required: true, message: '', trigger: 'blur' },
        ],
      },
      cateList: [],
      selectedKeys: [],
      editGoodsDialogVisible: false,
      editGoodsForm: {
        goods_name: '',
        goods_price: 0,
        goods_weight: 0,
        goods_number: 0, 
      },
      edit_goods_id:0,
    }
  },
  created() {
    this.getGoodsList()
  },
  methods: {
    //   
    async getGoodsList() {
      const { data: res } = await this.$http.get('goods', {
        params: this.queryInfo,
      })
      if (res.meta.status !== 200) {
        return this.$message.error('')
      }
      this.goodsList = res.data.goods
      // console.log(this.goodsList)
      this.total = res.data.total
    },
    // pagesize
    handleSizeChange(newSize) {
      this.queryInfo.pagesize = newSize
      this.getGoodsList()
    },
    // pagenum
    handleCurrentChange(newNum) {
      this.queryInfo.pagenum = newNum
      this.getGoodsList()
    },
    // 
    // showAddGoodsDialog() {
    //   this.getCateList()
    //   this.addGoodsDialogVisible = true
    // },
    // 
    handleChange() {
      console.log(this.selectedKeys)
    },
    // 
    async getCateList() {
      const { data: res } = await this.$http.get('categories', {
        params: { type: 3 },
      })
      if (res.meta.status !== 200) {
        return this.$message.error('')
      }
      this.cateList = res.data
      console.log(this.cateList)
    },
    // 
    // addGoods() {
    //   this.$refs.addGoodsFormRef.validate(async (valid) => {
    //     if (!valid) return
    //     const { data: res } = await this.$http.post(`goods`, {
    //       goods_name: this.addGoodsForm.goods_name,
    //       goods_price: this.addGoodsForm.goods_price,
    //       goods_weight: this.addGoodsForm.goods_weight,
    //       goods_number: this.addGoodsForm.goods_number,
    //       goods_cat: this.selectedKeys.join(','),
    //     })
    //     if (res.meta.status !== 201) {
    //       return this.$message.error('')
    //     }
    //     this.$message.success('')
    //     this.getGoodsList()
    //     this.addGoodsDialogVisible = false
    //   })
    // },
    // 
    async removeGoods(row) {
      const confirmResult = await this.$confirm(
        `${row.goods_name}, ?`,
        '',
        {
          confirmButtonText: '',
          cancelButtonText: '',
          type: 'warning',
        }
      ).catch((err) => err)
      if (confirmResult !== 'confirm') {
        return this.$message.info('')
      }
      console.log(row)
      const { data: res } = await this.$http.delete(`goods/${row.goods_id}`)
      if (res.meta.status !== 200) {
        return this.$message.error('')
      }
      this.$message.success('')
      this.getGoodsList()
    },
    // id,
    async showEditGoodsDialog(row) {
      this.editGoodsForm.goods_name = row.goods_name
      this.editGoodsForm.goods_weight = row.goods_weight
      this.editGoodsForm.goods_number = row.goods_number
      this.editGoodsForm.goods_price = row.goods_price
      this.edit_goods_id = row.goods_id
      this.editGoodsDialogVisible = true
      console.log(this.edit_goods_id)
      console.log(this.editGoodsForm)
    },
    // 
    async editGoods(){
      const {data:res} = await this.$http.put(`goods/${this.edit_goods_id}`, this.editGoodsForm)
      if(res.meta.status !== 200){
        return this.$message.error('')
      }
      this.$message.success('')
      this.getGoodsList()
      this.editGoodsDialogVisible = false
    },
    addPage(){
      this.$router.push('goods/add')
    }
  },
}
</script>

<style lang="less" scoped>
</style>