<template>
  <div>
    <!--  -->
    <el-breadcrumb separator-class="el-icon-arrow-right">
      <el-breadcrumb-item :to="{ path: '/welcome' }"></el-breadcrumb-item>
      <el-breadcrumb-item></el-breadcrumb-item>
      <el-breadcrumb-item></el-breadcrumb-item>
    </el-breadcrumb>

    <!--  -->
    <el-card>
      <!--  -->
      <el-row>
        <el-col>
          <el-button type="primary" @click="showAddCateDialog"
            ></el-button
          >
        </el-col>
      </el-row>

      <!--  -->
      <tree-table
        class="treeTable"
        :data="catesList"
        :columns="columns"
        :selection-type="false"
        :expand-type="false"
        show-index
        index-text="#"
        border
      >
        <template slot="isOk" slot-scope="scope">
          <i
            class="el-icon-success"
            v-if="scope.row.cat_deleted === false"
            style="color: green"
          ></i>
          <i class="el-icon-error" v-else></i>
        </template>
        <template slot="order" slot-scope="scope">
          <el-tag v-if="scope.row.cat_level === 0" size="mini"></el-tag>
          <el-tag
            type="success"
            v-else-if="scope.row.cat_level === 1"
            size="mini"
            ></el-tag
          >
          <el-tag type="warning" v-else size="mini"></el-tag>
        </template>
        <template slot="opt" slot-scope="scope">
          <el-button
            type="primary"
            class="el-icon-edit"
            size="mini"
            @click="showEditCateDialog(scope.row)"
            ></el-button
          >
          <el-button
            type="danger"
            class="el-icon-delete"
            size="mini"
            @click="removeCate(scope.row)"
            ></el-button
          >
        </template>
      </tree-table>

      <!--  -->
      <el-pagination
        @size-change="handleSizeChange"
        @current-change="handleCurrentChange"
        :current-page="queryInfo.pagenum"
        :page-sizes="[5, 10, 15, 20]"
        :page-size="queryInfo.pagesize"
        layout="total, sizes, prev, pager, next, jumper"
        :total="total"
      >
      </el-pagination>
    </el-card>

    <!--  -->
    <el-dialog
      title=""
      :visible.sync="addCateDialogVisible"
      width="50%"
      @close="addCateDialogClosed"
    >
      <!--  -->
      <el-form
        :model="addCateForm"
        :rules="addCateFormRules"
        ref="addCateFormRef"
        label-width="100px"
      >
        <el-form-item label="" prop="cat_name">
          <el-input v-model="addCateForm.cat_name"></el-input>
        </el-form-item>
        <el-form-item label="">
          <el-cascader
            @change="parentCateChanged"
            v-model="selectedKeys"
            :options="parentCateList"
            :props="{
              expandTrigger: 'hover',
              label: 'cat_name',
              value: 'cat_id',
              children: 'children',
              checkStrictly: true,
            }"
          ></el-cascader>
        </el-form-item>
      </el-form>
      <span slot="footer" class="dialog-footer">
        <el-button @click="addCateDialogVisible = false"> </el-button>
        <el-button type="primary" @click="addCate"> </el-button>
      </span>
    </el-dialog>

    <!--  -->
    <el-dialog title="" :visible.sync="editCateDialogVisible" width="50%">
      <!--  -->
      <el-form ref="editCateFormRef" :model="editCateForm" label-width="80px">
        <el-form-item label="">
          <el-input v-model="editCateForm.cat_name"></el-input>
        </el-form-item>
      </el-form>

      <span slot="footer" class="dialog-footer">
        <el-button @click="editCateDialogVisible = false"> </el-button>
        <el-button type="primary" @click="editCate"> </el-button>
      </span>
    </el-dialog>
  </div>
</template>

<script>
export default {
  data() {
    return {
      queryInfo: {
        type: 3,
        pagenum: 1,
        pagesize: 5,
      },
      catesList: [],
      total: 0,
      columns: [
        { label: '', prop: 'cat_name' },
        { label: '', type: 'template', template: 'isOk' },
        { label: '', type: 'template', template: 'order' },
        { label: '', type: 'template', template: 'opt' },
      ],
      addCateDialogVisible: false,
      addCateForm: {},
      addCateFormRules: {
        cat_name: [
          {
            required: true,
            message: '',
            trigger: 'blur',
          },
        ],
      },
      //   
      selectedKeys: [],
      // 
      addCateForm: {
        cat_name: '',
        cat_pid: 0,
        cat_level: 0,
      },
      parentCateList: [],
      editCateDialogVisible: false,
      editCateForm: {},
      // 
      cateInfo: {},
    }
  },
  created() {
    this.getCateList()
  },
  methods: {
    // 
    async getCateList() {
      const { data: res } = await this.$http.get('categories', {
        params: this.queryInfo,
      })
      if (res.meta.status !== 200) {
        return this.$message.error('')
      }
      this.catesList = res.data.result
      this.total = res.data.total
      //   console.log(this.catesList)
      console.log(this.catesList)
    },
    // 
    handleSizeChange(newSize) {
      this.queryInfo.pagesize = newSize
      this.getCateList()
    },
    // 
    handleCurrentChange(newNum) {
      this.queryInfo.pagenum = newNum
      this.getCateList()
    },
    // 
    showAddCateDialog() {
      this.getParentCateList()
      this.addCateDialogVisible = true
    },
    // 
    async getParentCateList() {
      const { data: res } = await this.$http.get('categories', {
        params: { type: 2 },
      })
      if (res.meta.status !== 200) {
        return this.$message.error('')
      }
      this.parentCateList = res.data
      console.log(res.data)
    },
    parentCateChanged() {
      console.log(this.selectedKeys)
      if (this.selectedKeys.length > 0) {
        this.addCateForm.cat_pid =
          this.selectedKeys[this.selectedKeys.length - 1]
        this.addCateForm.cat_level = this.selectedKeys.length
        return
      } else {
        this.addCateForm.cat_pid = 0
        this.addCateForm.cat_level = 0
      }
    },

    addCate() {
      this.$refs.addCateFormRef.validate(async (valid) => {
        if (!valid) return
        const { data: res } = await this.$http.post(
          `categories`,
          this.addCateForm
        )
        if (res.meta.status !== 201) {
          return this.$message.error('')
        }
        this.$message.success('')
        this.getCateList()
        this.addCateDialogVisible = false
      })
    },

    // 
    addCateDialogClosed() {
      console.log('ddd')
      this.$refs.addCateFormRef.resetFields()
      this.selectedKeys = []
      this.addCateForm.cat_pid = 0
      this.addCateForm.cat_level = 0
    },

    //  id
    async showEditCateDialog(cateInfo) {
      this.cateInfo = cateInfo
      const { data: res } = await this.$http.get(
        `categories/${cateInfo.cat_id}`
      )
      if (res.meta.status !== 200) {
        return this.$message.error('')
      }
      this.editCateForm = res.data
      console.log(this.editCateForm)
      // console.log(cateInfo)
      this.editCateDialogVisible = true
    },
    // 
    editCate() {
      this.$refs.editCateFormRef.validate(async (valid) => {
        if (!valid) return
        const { data: res } = await this.$http.put(
          `categories/${this.cateInfo.cat_id}`,
          {
            cat_name: this.editCateForm.cat_name,
          }
        )
        if (res.meta.status !== 200) {
          return this.$message.error('')
        }
        this.getCateList()
      })
      this.editCateDialogVisible = false
    },

    // 
    async removeCate(cateInfo) {
      const confirmResult = await this.$confirm(
        ', ?',
        '',
        {
          confirmButtonText: '',
          cancelButtonText: '',
          type: 'warning',
        }
      ).catch((err) => err)
      if(confirmResult !== 'confirm'){
        return this.$message.error('')
      }
      const {data:res} = await this.$http.delete(`categories/${cateInfo.cat_id}`)
      if(res.meta.status !== 200){
        return this.$message.error('')
      }
      this.$message.success('')
      this.getCateList()
    },
  },
}
</script>

<style lang="less" scoped>
.treeTable {
  margin-top: 15px;
}
</style>